*{
    padding: 0;
    margin: 0;
    scroll-behavior: smooth;
}
body{
    background-color: rgb(235,243,248);
}
.App{
    background-color: white;
    color: black;
}
.container{
    background-color: black;
    color: rgb(160,196,211);
}    
.containers{
    background-color: rgb(233, 239, 241);
    color: black;
}
.headin-coontainer{
    background-image: linear-gradient(0deg ,rgba(255, 255, 255, 0.151) ,rgb(174, 178, 180));
    display: flex;
    position: fixed;
    width: 100%;
    z-index: 5;
    background-color: rgba(255, 255, 255, 0.151);
}
.heading{
    width: 50%;
    padding:  0 0 0 5%;
}
.heading>p{
    font-size: 220%;
}
.mode{
    width: 50%;
    display: flex;
    cursor: pointer;
    align-items: baseline;
}
.span{
    font-size: x-large;
    vertical-align: baseline;
}
.Pro{
    color: gray;
}
.toggle{
    color: blue;
    font-size: 180%;
    vertical-align: middle;
}
.btn{
    background-color: none;
    font-size: x-large;
    vertical-align: baseline;
}
.main-holder{
    display: grid;
    grid-template-columns: auto auto;
}
.Sidebar-container{
    width: 10rem;
    padding: 5% 0 0 0;
    cursor: pointer;
    position: fixed;
}
.Sidebar-items{
    padding: 20%;
    border-right: 2px solid grey;
    font-weight: bolder;
}
.selected {
    background-color:  grey;
    color: white;
  }
.Sidebar-items:hover{
    background-color:  grey;
    color: white;
}
a{
    text-decoration: none;
    color: rgb(233, 200, 200);
}
.complete-content-holder{
    position: absolute;
    top: 5%;
    left: 11%;
}
.content-container{
    padding: 0 0 0 0%;
}
.Hero-bg{
    width: 98%;
}
.Hero-Banner{
    position: absolute;
    z-index: 1;
    left: 12%;
    top: 8%;
}
@keyframes typing {
    from{width: 0%;}
    to{width: 100%;}
}
.Hero-banner-text{
    color: rgb(160,196,211);
    border-right: 5px solid rgb(206,88,91);
    text-shadow: 10px 10px 20px rgb(69,103,134);
    overflow: hidden;
    animation: typing;
    animation-duration: 10s;
    animation-iteration-count: infinite;
    animation-direction: alternate;
    animation-timing-function: steps(35);
    font-family: 'Caprasimo', cursive;
    font-size: 300%;
}


.aboutme-holder{
    display: flex;
    position: absolute;
    top: 32%;
}
.photoHolder{
    border-right: 5px solid gold;
    padding: 0 5% 0 5%;
}
.myImage{
    border-radius: 10rem;
}
.para{
    text-align: justify;
    font-size: larger;
    margin: 0 3% 0 1% ;
    padding:  5% 0 0 0;
}

.Table-holder{
    position: absolute;
    top: 52%;
    left: 15%;
}
.Table-container{
    display: grid;
    grid-template-columns: auto auto;
}

.Project-holder,
.skills-holder{
    width: 450px;
    height: 200px;
    overflow: hidden;
}
.TableImgTag{
    position: absolute;
    z-index: 5;
    top: 0%;
    width: 450px;
    height: 30px;
    font-size: large;
    text-align: center;
    font-weight: bolder;
}
.skills-holder{
    margin: 0 0 0 5%;
}
.TableImg{
    width: 100%;
    transition: 0.1s all ease-in-out;
}
.TableImg:hover{
    transform: scale(1.4);
}

.project-container{
    display: flex;
    margin:5% 0 0 0;
}
.project-holder{
    margin: 0 5% 0 0;
    width: 250px;
    height: 150px; 
    overflow: hidden;
}
.project-holder>a{
    text-decoration: none;
    color: black;
}
.project-holder:hover{
    .project-img-overview{
        transform: scale(1.2);
        transition: 0.1s all ease-in-out;
    }
    .project-Name{
        opacity: 1;
        transition: 0.1s all ease-in-out;
        background-color: rgba(240, 248, 255, 0.253);
        font-family: 'Caprasimo', cursive;
    }
}
.project-img-overview{
    width: 250px;
}
.project-Name{
    position: relative;
    text-align: center;
    font-size: xx-large;
    font-weight: bolder;
    bottom: 15%;
    left: 0%;
    opacity: 0;
}
.project-Name-Para{
    font-size: small;
}
.Contactme-Section{
    position: absolute;
    top: 80%;
    display: flex;
}
.quote-holder{
    text-align: center;
    font-size: 300%;
    font-weight: bolder;
    padding: 5% 2% 0 2%;
    border-right: 2px solid gold;
}
.form-container{
    margin: 0 0 0 10%;
    border: 1px solid gray;
    padding: 5%;
    width:100%;
}
.input-area{
    padding: 2%;
    font-weight: bolder;
    border: none;
    border-bottom: 2px solid rgb(110, 97, 97);
    background-color: rgba(250, 235, 215, 0.123);
    color: rgb(148, 144, 144);
    font-size: large;
}
.input-area.child{
    grid-row-start: 1,0;
}
form{
    display: grid;
}
.or{
    text-align: center;
}
.frm-btn{
    width:100%;
    padding: 2%;
    font-size: large;
    font-weight: bolder;
}
.frm-btn:hover{
    background-color: grey;
    color: white;
}

.social-holder{
    display: flex;
    margin: 0 0 0 25%;
}
@keyframes opac {
    0%{opacity: 0;}
    100%{opacity: 1;}
}
.social-btn{
    animation: opac;
    animation-duration: 1s;
    margin: 2%;
    padding: 5%;
    background-color: cadetblue;
    font-size: x-large;
    border-radius: 1rem 0 0 0 ;
}
.social-btn:hover{
    background-color: rgb(140, 200, 202);
}

.Footer-container{
    position: fixed;
    bottom: 0;
    display: grid;
    grid-template-columns: auto auto;
    width: 100%;
}
.footer-container-item{
    text-align: center;
    font-size: x-large;
    font-weight: bolder;
}
span{
    color: red;
}